import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import {RouterModule, Routes} from '@angular/router';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { LoginComponent } from './login/login.component';
import {FlashMessagesModule,FlashMessagesService} from 'angular2-flash-messages';

// Service
import { AuthService } from './services/auth.service';
import { GetjsonService } from './services/getjson.service';
import { ValidateService } from './services/validate.service';
import { PhoneverifyService } from './services/phoneverify.service';
import { KchartService } from './services/kchart.service';

import { RegisterComponent } from './register/register.component';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { KchartComponent } from './kchart/kchart.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';

const appRoutes : Routes = [
  {path:'', component: HomeComponent},
  {path:'register', component: RegisterComponent},
  {path:'login', component: LoginComponent},
  {path:'about', component: AboutComponent},
  {path:'kchart', component: KchartComponent},
  {path:'forgotpass', component: ForgotPasswordComponent},
];

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    AboutComponent,
    KchartComponent,
    ForgotPasswordComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FlashMessagesModule
  ],
  providers: [
    AuthService,
    ValidateService,
    GetjsonService,
    PhoneverifyService,
    FlashMessagesService,
    KchartService
  ],
  bootstrap: [
    AppComponent
  ]
})
export class AppModule { }



