import { Injectable } from '@angular/core';
import { Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import { environment } from '../../environments/environment';

@Injectable()
export class PhoneverifyService {

  // m_ServerUrl = environment.m_ServerUrl;
  // m_ServerUrl = "http://localhost:3000";
  m_ServerUrl = "http://47.75.138.128:3000";
  
  constructor(private http: Http) { }
  
  sendVerificationCode( phoneNumber : String){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    
    return this.http.post(this.m_ServerUrl + '/user/sendsms', { "phone": phoneNumber } , {headers : headers})
      .map(res => {
        return res.json();
      });
  }

}



