import { Component, OnInit } from '@angular/core';
import { AuthService } from'../services/auth.service';
import { PhoneverifyService } from'../services/phoneverify.service';
import { ValidateService } from'../services/validate.service';
import { Router } from '@angular/router';
// import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  emailorphone : String;
  password : String;

  loginType : boolean; // True: Phone, False: Email
  phonenumber : String;
  verifycode : String;
  resend : boolean;

  constructor(private authService : AuthService,
              private phoneService : PhoneverifyService,
              private validateService : ValidateService,
              private router : Router
              // private flashMessage:FlashMessagesService
  ) { }

  ngOnInit() {

    this.loginType = false;
    this.resend = false;

    if(this.authService.loggedIn() == true)
      this.router.navigate(['kchart']);

  }
  
  sendVerifyCode() {

    this.resend = true; // Enable Resend Verify Code Button.
    if(this.validateService.validatePhoneNumber(this.phonenumber)){
      this.phoneService.sendVerificationCode(this.phonenumber).subscribe(data => {
        if(data.success && JSON.parse(data.body)["returnstatus"] == "Success") {
          console.log(data);
        } else {
          alert(JSON.parse(data.body));
        }
      });
    } else {
      alert("再次检查您的电话号码");
    }
  }

  onLoginSubmit(){
    const user = {
      emailorphone : this.emailorphone,
      password : this.password
    };
    this.authService.authenticateUser(user).subscribe(data=>{
      if(data.success) {
        this.authService.storeUserData(data.token, data.user);
        this.router.navigate(['kchart']);
      } else {
        alert("Failed");
        this.router.navigate(['login']);
      }
    });
  }
  onPhoneLoginSubmit() {
    const phone = {
      phone : this.phonenumber,
      vcode : this.verifycode
    }
    this.authService.authenticatePhone(phone).subscribe(data=>{
      if(data.success){
        this.authService.storePhoneData(data.token, phone);
        this.router.navigate(['kchart']);
      } else {
        alert("Verify code not matching");
      }
    });
  }

  forgotPassword(){
    this.router.navigate(['forgotpass']);
  }
}



