import { Injectable } from '@angular/core';
import { Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';
import { environment } from '../../environments/environment'

//declare var AuthGuard: any;

@Injectable()
export class KchartService {
    authToken: any;
    // m_ServerUrl = environment.m_ServerUrl;
    // m_ServerUrl = "http://localhost:3000";
    m_ServerUrl = "http://47.75.138.128:3000";

    constructor(private http: Http) { 
        this.loadToken();
    }

    loadToken(){
        const token = localStorage.getItem('id_token');
        this.authToken = token;
    }

    findSimilarchart(data){
        let headers = new Headers();

        // headers.append('Authorization', this.authToken);
        headers.append('Content-Type', 'application/json');
        // 1: open
        // 2: low
        // 3: high
        // 4: close
        console.log(data);
        return this.http.post(this.m_ServerUrl + '/user/findsimilarchart/', data, {headers : headers})
        .map(res => res.json());
    }
}


