import { Component, OnInit } from '@angular/core';
import { AuthService } from'../services/auth.service';
import { PhoneverifyService } from'../services/phoneverify.service';
import { ValidateService } from'../services/validate.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {

  resend : boolean;
  resetPass : boolean;
  phonenumber : String;
  verifycode : String;
  newpass : String;
  confirmpass : String;

  constructor(private authService : AuthService,
              private phoneService : PhoneverifyService,
              private validateService : ValidateService,
              private router : Router
              // private flashMessage:FlashMessagesService
  ) { }

  ngOnInit() {
    this.resend = false;
    this.resetPass = false;
  }

  onSendVerifyCode() {
    if(this.validateService.validatePhoneNumber(this.phonenumber)){
      this.resend = true; // Enable Resend Verify Code Button.
      this.phoneService.sendVerificationCode(this.phonenumber).subscribe(data => {
        if(data.success && JSON.parse(data.body)["returnstatus"] == "Success") {
          console.log(data);
        } else {
          alert(JSON.parse(data.body));
        }
      });
    } else {
      alert("再次检查您的电话号码");
    }
  }
  onVerify() {
    const phone = {
      phone : this.phonenumber,
      vcode : this.verifycode
    }
    this.authService.authenticatePhone(phone).subscribe(data=>{
      if(data.success) {
        this.resetPass = true;
        // this.authService.resetPassword(phone).subscribe(data1 => {

        // });
      } else {
        alert("Verify code not matching");
      }
    });
  }

  onResetPass() {
    if(this.newpass == this.confirmpass){
      const phone = {
        phone: this.phonenumber,
        password : this.newpass
      }
      this.authService.resetPassword(phone).subscribe( data => {
        if(data.success){
          alert("Password Reset");
          this.router.navigate(['login']);
        } else {
          alert("Password not set, input the original phone number you use");
        }
      });
    } else {
      alert("Password didn't match");
    }

  }
}



