import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

@Injectable()
export class GetjsonService {

  constructor(private http: Http) { }

  getJsonData() {
    let url = "/assets/data.json";
    return this.http.get(url)
    .map(res  => res.json());
  }
}



