import { Injectable } from '@angular/core';
import { Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';
import { environment } from '../../environments/environment'
//declare var AuthGuard: any;

@Injectable()
export class AuthService {
  authToken: any;
  user: any;
  phone: any;
  
  // m_ServerUrl = environment.m_ServerUrl;
  m_ServerUrl = "http://47.75.138.128:3000";
  constructor(private http: Http) { 

  }

  getVideo(){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get(this.m_ServerUrl + '/user/getVideo', {headers : headers})
      .map(res => res.json());
  }
  
  uploadImage(uploadData) {
    console.log("Upload Image");  
    // alert(uploadData.emailorphone);
    // alert(uploadData.filename);
    let headers = new Headers();
    headers.append('Content-Type', undefined);
    let formData = new FormData();
    formData.append("image", uploadData.image);
    formData.append("emailorphone", uploadData.emailorphone);
    formData.append("filename", uploadData.filename);

    console.log(formData);
    return this.http.post(this.m_ServerUrl + '/user/uploadImage', formData)
      .map(res => res.json());
  }

  registerUser(user){    
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post(this.m_ServerUrl + '/user/register', user, {headers : headers})
      .map(res => res.json());
  }

  authenticateUser(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post(this.m_ServerUrl + '/user/authenticate', user, {headers : headers})
      .map(res => res.json());
  }

  authenticatePhone( phone ){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post(this.m_ServerUrl + '/user/authenticatebyphone', phone, {headers : headers})
      .map(res => res.json());
  }

  resetPassword( phone ){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post(this.m_ServerUrl + '/user/resetpass', phone, {headers : headers})
      .map(res => res.json());
  }

  getProfile(){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get(this.m_ServerUrl + '/user/profile', {headers : headers})
      .map(res => res.json());
  }
  storeUserData(token, user){
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  storePhoneData(token, phone) {
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify({name: phone.phone}));
    this.authToken = token;
    this.phone = phone;
  }

  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  getLoggedInUser() {
    this.user = JSON.parse(localStorage.getItem('user'));
    return this.user;
  }

  loggedIn(){
    return tokenNotExpired('id_token');
  }

  logout(){
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }
}



